<template>
  <div class="wrapper">
    <div class="content">
      <div class="head1" style="font-size: 22px">
        <h1>模版字符串</h1>
        <h2>(该笔记适用于Vue开发环境)</h2>
      </div>
       <Card>
      <div class="point1">
        <ul>
          <li>
            ES6引入的模板字符串，模板字符串用反引号（`）标识,
            既可以当做普通字符串使用也可以用来定义多行字符，
            或者在字符串里嵌入变量。
          </li>
          data() {
          <br />
          return { name: `jsx`};<br />
          }
          <br />
          console.log(`${this.name}`);
          <li>在模板字符串中需要使用反引号，则前面要用反斜杠转义 \</li>
          <li>
            使用模板字符串表示多行字符串，所有的空格和缩进都会被保留,可以使用
            trim 方法消除前后空格，正则去除空格缩进
          </li>
          <li>模板字符串嵌入变量，需要将变量写在 ${} 之中</li>
          <li>模板字符串之中也能调用函数</li>
          methods: { <br />
            fn() { <br />
              return "hello"; <br />},<br />
             }
             <br />
             console.log(`${this.fn()}`);
             <li>模板字符串可以嵌套使用</li>
             data() {
          <br />
          return { <br />
          str: `${['jsx', 'ljj'].map((a) => `${a}`)}`
          <br />};
          }
          <br />
          console.log(`${this.str}`);
        </ul>
      </div>
       </Card>
    </div>
    
  </div>
</template>

<script>
export default {
  name: "Codes",
  created() {},
  data() {
    return {
      name: `jsx`,
      str: `${['jsx', 'ljj'].map((a) => `${a}`)}`
    };
  },
  methods: {
    fn() {
      return "hello";
    },
  },
  mounted() {
    console.log(`${this.name}`);
    console.log(`${this.fn()}`);
    console.log(`${this.str}`);
  },
};
</script>

<style>
* {
  box-sizing: border-box;
}
.content {
  width: 1200px;
  margin: 0 auto;

}
.head1 {
  width: 100%;
  text-align: center;
}
.point1 {
  font-size: 26px;
  padding: 12px;

}
</style>



